body, html {
    background-color: #f5f5f5;
    height: 100%
}

body {
    color: #333;
    font-family: "Microsoft YaHei", Arial, Verdana, serif;
    font-size: 14px;
    line-height: 1.4;
    -webkit-user-select: none;
    -webkit-touch-callout: none
}

body {
    color: #333;
    font-family: "Microsoft YaHei", Arial, Verdana, serif;
    font-size: 14px;
    line-height: 1.4;
    -webkit-user-select: none;
    -webkit-touch-callout: none
}

@media (max-width: 768px) {
    .nav-haeder {
        font-family: NotoSansHans-Medium;
        font-size: 15px;
        font-weight: 700;
        text-align: center;
        height: 50px;
        line-height: 50px;
        color: #000;
        background: rgba(255, 255, 255, 1)
    }

    .nav-haeder-goback {
        position: relative;
        top: 4px;
        right: 36%;
        width: 11px;
        height: 20px;
        display: inline-block;
      //  background: url(../images/goback.png) no-repeat;
        background-size: 100% 100%
    }

    .tabs {
        font-size: 15px;
        margin-top: 2px;
        color: #4d4c4c
    }

    .tabs ul {
        background: rgba(255, 255, 255, 1)
    }

    .tabs li {
        height: 45px;
        float: left;
        line-height: 45px;
        width: 33.33%;
        list-style: none;
        text-align: center;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box
    }

    .active {
        border-bottom: 2px #ff5a6d solid
    }

    .article images {
        width: 80%
    }

    .article {
        font-family: Noto Sans S Chinese;
        margin-top: 15px;
        font-size: 13px;
        font-weight: 700;
        width: 100%;
        height: 100%;
        text-align: center;
        line-height: 30px
    }

    .article-txt {
        margin-bottom: 10px
    }

    #business {
        margin-top: 0;
        line-height: 20px;
        text-align: center
    }

    .business-images1 images {
        height: 16px;
        width: 77px;
        margin-top: 10px;
        margin-bottom: 10px
    }

    .business-images2 images {
        height: 16px;
        width: 100px;
        margin-top: 10px;
        margin-bottom: 10px
    }

    #business .business-txt {
        text-indent: 2em;
        text-align: left;
        line-height: 20px;
        margin: 20px 25px
    }

    .slide-content {
        margin-top: -10px
    }
}

@media (min-width: 768px) {
    .wrapper {
        width: 100%;
        display: block;
        min-width: 320px
    }

    .main {
        margin: 0 auto
    }

    #wrap {
        position: relative;
        min-height: 100%;
        height: 100%;
        overflow: hidden
    }

    #pages {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center
    }

    .page {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%
    }

    .wrapper {
        width: 100%;
        display: block;
        min-width: 320px
    }

    .main {
        margin: 0 auto
    }

    #wrap {
        position: relative;
        min-height: 100%;
        overflow: hidden
    }

    #pages {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center
    }

    .page {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%
    }

    .navbar {
        height: 90px;
        background-color: #fff;
        line-height: 90px;
        text-align: left;
        box-shadow: 1px 1px 10px #888
    }

    .nav-logo images {
        width: 240px;
        height: 72px;
        vertical-align: middle
    }

    .navbar a {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        font-weight: 500;
        font-size: 20px;
        line-height: 20px;
        text-decoration: none;
        display: inline-block;
        border-radius: 10px;
        padding: 10px 15px;
        margin-right: 5px;
        font-family: Adobe Heiti Std R;
        color: #6b7a82
    }

    .navbar a:nth-child(2) {
        font-size: 20px;
        line-height: 22px;
        text-decoration: none;
        display: inline-block;
        border-radius: 10px;
        padding: 10px 15px;
        margin-right: 5px;
        font-family: Adobe Heiti Std R;
        color: rgba(255, 254, 254, 1);
        background: #fee56f
    }

    .navbar a:hover {
        font-size: 20px;
        line-height: 22px;
        text-decoration: none;
        display: inline-block;
        border-radius: 10px;
        padding: 10px 15px;
        margin-right: 5px;
        font-family: Adobe Heiti Std R;
        color: rgba(255, 254, 254, 1);
        background: #fee56f
    }

    .nav-txt > :first-child {
        margin-right: 8px
    }

    .page1-title {
        font-size: 48px;
        font-weight: 700;
        font-family: NotoSansHans-Bold;
        color: #6b6b6b;
        margin-top: 40px
    }

    .page3-title {
        font-size: 48px;
        font-weight: 700;
        font-family: NotoSansHans-Bold;
        color: #6b6b6b;
        margin-top: 50px
    }

    .content {
        width: 1120px;
        text-align: center;
        font-family: Noto Sans S Chinese;
        font-size: 18px;
        line-height: 50px;
        color: #000;
        margin: 60px auto;
        background-color: #fff
    }

    .page {
        background-color: #f5f5f5
    }

    .page1-content {
        padding-top: 135px;
        height: 582px;
        background: url(../images/by.png) no-repeat;
        background-size: 100% 100%
    }

    .tag1 {
        //background: url(../images/about_company.png) no-repeat;
        background-size: 100% 100%
    }

    .page2-content {
        font-family: SimHei;
        margin-top: 10px;
        padding-top: 90px;
        padding-bottom: 36px;
        line-height: 70px;
        background: url(../images/by_2.png) no-repeat;
        background-size: 100% 100%
    }

    .page2-content images {
        margin-top: 10px;
        margin-bottom: 35px
    }

    .page2-dashed {
        height: 10px;
        width: 60%;
        background: url(../images/dashed.png) no-repeat;
        margin: 30px auto 30px auto
    }

    .page3-content {
        font-family: SimHei;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        margin-top: 30px;
        line-height: 40px;
        height: 54%;
        padding-top: 100px;
        padding-bottom: 100px
    }

    .page-footer {
        position: absolute;
        width: 100%;
        height: 104px;
        bottom: 0;
        left: 0;
        background: rgba(230, 230, 230, 1)
    }

    .page-footer-content {
        font-size: 18px;
        font-family: MicrosoftYaHei;
        line-height: 18px;
        width: 802px;
        margin: 5px auto;
        font-size: 14px;
        text-align: center;
        width: 100%;
        padding: 5px;
        line-height: 25px;
        color: #6b6b6b
    }

    .page-footer-content span {
        margin-right: 30px
    }

    .contact span {
        margin-right: 20px
    }
}

